const AvailableMeals = () => {
  const [mealsData, setmealsData] = useState([]);
  const [httpError, sethttpError] = useState(null);
  const [isLoading, setisLoading] = useState(true);

  const fetchFoodList = useCallback(async () => {
    sethttpError(null);
    const foodListResponse = await fetch(
      "https://foodorder-b4181-default-rtdb.firebaseio.com/meals.json"
    );

    if (!foodListResponse.ok) {
      throw new Error("Something went wrong!");
    }
    const data = await foodListResponse.json();
    const listData = [];
    for (const key in data) {
      listData.push({
        id: key,
        name: data[key].name,
        description: data[key].description,
        price: data[key].price,
      });
    }
    setmealsData(listData);
    setisLoading(false);
  }, []);
  useEffect(() => {
    try {
      fetchFoodList().catch((error) => {
        sethttpError(error.message);
        setisLoading(false);
      });
    } catch (error) {
      debugger;
      sethttpError(error.message);
      setisLoading(false);
    }
  }, [fetchFoodList]);
  if (isLoading) {
    return (
      <section className={classes.MealsLoading}>
        <p>Loading...</p>
      </section>
    );
  }
  if (httpError) {
    return (
      <section className={classes.MealsError}>
        <p>{httpError}</p>
      </section>
    );
  }
  const mealsList = mealsData.map((meal, item) => (
    <MealItem
      key={meal.id}
      id={meal.id}
      name={meal.name}
      description={meal.description}
      price={meal.price}
    />
  ));

  return (
    <section className={classes.meals}>
      <Card>
        {/* <ul>{isLoading ? "Loading.." : { mealsList }}</ul> */}
        {mealsList}
        {httpError && <p>Something went wrong!</p>}
      </Card>
    </section>
  );
};

export default AvailableMeals;
