import React, { useRef, useState } from "react";
import classes from "./Checkout.module.css";
//import Modal from "../UI/Modal";
//import CartContext from "../../store/cart-context";
const isEmpty = (value) => value.trim() === "";
const isFiveChar = (value) => value.trim().length === 5;
const Checkout = (props) => {
  const nameInputRef = useRef();
  const streetInputRef = useRef();
  const postalCodeInputRef = useRef();
  const cityInputRef = useRef();
  const [formInputValidity, setformInputValidity] = useState({
    name: true,
    street: true,
    city: true,
    postalcode: true,
  });
  const confirmHandler = (event) => {
    ///////////////////////////////////////
    event.preventDefault();
    const enterName = nameInputRef.current.value;
    const enterStreet = streetInputRef.current.value;
    const entercity = cityInputRef.current.value;
    const enterPostalCode = postalCodeInputRef.current.value;

    const enterNameIsValid = !isEmpty(enterName);
    const enterStreetIsValid = !isEmpty(enterStreet);
    const entercityIsValid = !isEmpty(entercity);
    const enterPostalCodeIsValid = isFiveChar(enterPostalCode);

    setformInputValidity({
      name: enterNameIsValid,
      street: enterStreetIsValid,
      city: entercityIsValid,
      postalcode: enterPostalCodeIsValid,
    });

    const formIsvalid =
      enterNameIsValid &&
      enterStreetIsValid &&
      entercityIsValid &&
      enterPostalCodeIsValid;

    if (!formIsvalid) {
      return;
    }
    //////////////////////////////////
    event.preventDefault();
    console.log(JSON.stringify(props.ListItem));
    console.log(nameInputRef.current.value);

    const orderConfirm = {
      name: nameInputRef.current.value,
      city: cityInputRef.current.value,
      street: streetInputRef.current.value,
      postalcode: postalCodeInputRef.current.value,
      orderItem: {},
    };
    props.ListItem.forEach(function (item, index) {
      orderConfirm.orderItem[props.ListItem[index].name] = {
        quantity: props.ListItem[index].amount,
        singleprice: props.ListItem[index].price,
        totalprice:
          parseInt(props.ListItem[index].amount) *
          parseFloat(props.ListItem[index].price),
      };
    });
    console.log(JSON.stringify(orderConfirm));
    props.onConfirm(orderConfirm);
  };

  return (
    <React.Fragment>
      <form className={classes.form} onSubmit={confirmHandler}>
        <div className={classes.control}>
          <label
            htmlFor="name"
            className={!formInputValidity.name ? classes.isNotValid : ""}
          >
            Your Name
          </label>
          <input type="text" id="name" ref={nameInputRef} />
        </div>
        <div className={classes.control}>
          <label
            htmlFor="street"
            className={!formInputValidity.street ? classes.isNotValid : ""}
          >
            Street
          </label>
          <input type="text" id="street" ref={streetInputRef} />
        </div>
        <div className={classes.control}>
          <label
            htmlFor="postal"
            className={!formInputValidity.postalcode ? classes.isNotValid : ""}
          >
            Postal Code
          </label>
          <input type="text" id="postal" ref={postalCodeInputRef} />
        </div>
        <div className={classes.control}>
          <label
            htmlFor="city"
            className={!formInputValidity.city ? classes.isNotValid : ""}
          >
            City
          </label>
          <input type="text" id="city" ref={cityInputRef} />
        </div>
        <div className={classes.actions}>
          <button type="button" onClick={props.onCancel}>
            Cancel
          </button>
          <button className={classes.submit}>Confirm</button>
        </div>
      </form>
    </React.Fragment>
  );
};
export default Checkout;
